import React, { useEffect, useState } from "react";
import { getStore, updStore } from "../stores/Utils";


const ObserveEvent = (input) => {
	const [value, setValue] = useState("");
	useEffect(() => {
		// To trigger the event Listener
		document.addEventListener('store_rdx', (e)=>{
			if(e.detail.id===input && e.detail.hash!==getStore(input)){
				updStore(input,e.detail.hash);
				setValue(()=>e.detail.hash);
			}
		});
		return () => {
			document.removeEventListener('store_rdx', ()=>{})
		};
	}, [input]);
	return value;
};

export default ObserveEvent;
