
import { useState, useEffect } from 'react'
import { fetchPostH, getUrlServerSide, updStore, fetchEncryptStream, tokenNameCookie, Base64Encode, decryptV4, callDispatchEvent, qryStore, getStore } from '../stores/Utils';



export const initQry = async (cb) => {
  let bdyq2 = {
      query: "53hvscu362"
  };
  const res = fetchEncryptStream(bdyq2);
  const td = await res;
  if (td && !td.error) {
      let qcb = getStore(qryStore()) || {};
      let updQry = Object.assign({},qcb, td)
      updStore(qryStore(), updQry);
  }
  cb && cb();
};






function GoogleSingIn() {
    const [count, setCount] = useState(0)
  
    const [view, setView] = useState(1)


    const validateTkn = async (token) =>{
        let Qry_V = getStore(qryStore())?.validateTkn;
        if(Qry_V){
          Qry_V.params = {
            tkn:  token
          }
          const resV = fetchEncryptStream(Qry_V);
          const tdV = await resV;
          if (tdV && tdV.userId) {
              updStore("profileUser",tdV)
              updStore("isAdmin",tdV.isAdmin)
              callDispatchEvent("isLogged_987");
          }
        }
    }



    const handleCredentialResponse = async (response) =>{
        let access_token = response.credential?.slice(response.credential.toString().length-32, response.credential.toString().length-16);  
        
        access_token = response.credential?.slice(0,16);  
        
        let Qry = {
            params:{
              id_token:response.credential
            },
            query:"getGoogleUserbyToken"
        };
       
        const res = fetchPostH(getUrlServerSide(), Qry);
        const td = await res;
        if (td && td.data) {
            let _now = new Date().getTime();
			let _expire = new Date(_now + 60000 * 60 * 24 * 365);
            let ckNm = tokenNameCookie();
            let decrypkey = decryptV4(td.data.split(":")[1],access_token);
            updStore("sectionKey",decrypkey)
            document.cookie = `${ckNm}=${td.data}; expires=${_expire}; path=/;g_state = {"i_l":1,"i_p":${_expire}}`;
            
            initQry(()=>{
              validateTkn(td.data)
            })
            
        }
    }
  
    useEffect(() => {
        let google = window.google;
        google?.accounts.id.initialize({
            client_id: "195275085181-8ccjpgp8v1g80qhnlk6v4lpgid7n81gr.apps.googleusercontent.com",
            callback: handleCredentialResponse
          });
          google?.accounts.id.renderButton(
            document.getElementById("buttonDiv"),
            { theme: "outline", size: "large" }  // customization attributes
          );
          //google.accounts.id.prompt(); // also display the One Tap dialog
      return () => {
        //
      }
    }, [])
    
    
  
    return (
      <div className="">
        <div id="buttonDiv"></div> 
      </div>
    )
  }
  
  export default GoogleSingIn
  