  export function parseDateMMDDYYYY(d) {
    var tp = new Date(d);
    let mm = tp.getMonth() + 1;
    let dd = tp.getDate();
    return `${mm>9?mm:"0"+mm}/${dd>9?dd:"0"+dd}/${tp.getFullYear()}`;
  }


  export function check_cookie_name(name) {
    var match = document.cookie.match(new RegExp("(^| )" + name + "=([^;]+)"));
    if (match) {
      return match[2];
    } else {
      return null;
    }
  }

