export const fetchPostH = async (url, bdy) => {
  let authToken =
    check_cookie_name(tokenNameCookie()) ||
    window.localStorage.getItem(tokenNameCookie());
  let opt = {
    method: "post",
    headers: {
      "Content-Type": "application/json",
      Authorization: `${authToken}`,
      "fingerprint": getStore("fingerprintID"),
    },
    body: JSON.stringify(bdy),
  }
  const res = await fetch(url, opt);
  return res.json();
};










