export function Base64Decode(str) {
  if (!/^[a-z0-9+/]+={0,2}$/i.test(str) || str.length % 4 != 0)
    throw Error("Not base64 string");

  var b64 = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";
  var o1,
    o2,
    o3,
    h1,
    h2,
    h3,
    h4,
    bits,
    d = [];

  for (var c = 0; c < str.length; c += 4) {
    // unpack four hexets into three octets
    h1 = b64.indexOf(str.charAt(c));
    h2 = b64.indexOf(str.charAt(c + 1));
    h3 = b64.indexOf(str.charAt(c + 2));
    h4 = b64.indexOf(str.charAt(c + 3));

    bits = (h1 << 18) | (h2 << 12) | (h3 << 6) | h4;

    o1 = (bits >>> 16) & 0xff;
    o2 = (bits >>> 8) & 0xff;
    o3 = bits & 0xff;

    d[c / 4] = String.fromCharCode(o1, o2, o3);
    // check for padding
    if (h4 == 0x40) d[c / 4] = String.fromCharCode(o1, o2);
    if (h3 == 0x40) d[c / 4] = String.fromCharCode(o1);
  }
  str = d.join(""); // use Array.join() for better performance than repeated string appends

  return str;
}

export function Base64Encode(str) {
  if (/([^\u0000-\u00ff])/.test(str)) throw Error("String must be ASCII");

  var b64 = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";
  var o1,
    o2,
    o3,
    bits,
    h1,
    h2,
    h3,
    h4,
    e = [],
    pad = "",
    c;

  c = str.length % 3; // pad string to length of multiple of 3
  if (c > 0) {
    while (c++ < 3) {
      pad += "=";
      str += "\0";
    }
  }
  // note: doing padding here saves us doing special-case packing for trailing 1 or 2 chars

  for (c = 0; c < str.length; c += 3) {
    // pack three octets into four hexets
    o1 = str.charCodeAt(c);
    o2 = str.charCodeAt(c + 1);
    o3 = str.charCodeAt(c + 2);

    bits = (o1 << 16) | (o2 << 8) | o3;

    h1 = (bits >> 18) & 0x3f;
    h2 = (bits >> 12) & 0x3f;
    h3 = (bits >> 6) & 0x3f;
    h4 = bits & 0x3f;

    // use hextets to index into code string
    e[c / 3] =
      b64.charAt(h1) + b64.charAt(h2) + b64.charAt(h3) + b64.charAt(h4);
  }
  str = e.join(""); // use Array.join() for better performance than repeated string appends

  // replace 'A's from padded nulls with '='s
  str = str.slice(0, str.length - pad.length) + pad;

  return str;
}




  export function encryptV4(plaintText,encryptedBase64Key ) {
    try{
      var parsedBase64Key = CryptoJS.enc.Base64.parse(encryptedBase64Key);
      var encryptedData = null;  
      encryptedData = CryptoJS.AES.encrypt(plaintText, parsedBase64Key, {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7
      });
      return encryptedData.ciphertext.toString(CryptoJS.enc.Base64);
    }
    catch(e){
      return null;
    }
  }
  
  
  export function decryptV4(encryptedCipherText, encryptedBase64Key ) {
    var parsedBase64Key = CryptoJS.enc.Base64.parse(encryptedBase64Key);
    var decryptedData = CryptoJS.AES.decrypt( encryptedCipherText, parsedBase64Key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7
    });
    return decryptedData.toString( CryptoJS.enc.Utf8 );
  }
  
  
  


  
export const fetchEncryptStream  = async (graphParams) => {
  
  const serializedParams = JSON.stringify(graphParams); 
  
  var resp = {};
 
  let sKey = getStore("sectionKey");
  var IdCrypt = gen16CodeId();
  let encryptKey = encryptV4(IdCrypt,sKey);      
  let encryptData = encryptV4(serializedParams,IdCrypt);
  const rs = {};
  rs["d"] = encryptData;
  rs["k"] = encryptKey;
  var bsParams = JSON.stringify(rs);


  var authToken = check_cookie_name(tokenNameCookie());
  const graphQLUrl = baseUrl+"gql_v4";
  if(encryptData){
    const res = await fetch(graphQLUrl, {
      method: 'post',
      headers: {
        //'Content-Type': 'text/plain',
        "Accept-Encoding": "gzip, deflate, br",
        'Content-Type': 'application/json',
        'Authorization': `${authToken}`,            
      },
      body: bsParams
    });
    let resJSON = await res.json();    
    var _Data = resJSON;
    const {data} =  _Data; 
    if(data && data.d){
      let decrypkey = decryptV4(data.k,sKey);    
      let rslt = decryptV4(data.d,decrypkey); 
      if(isJson(rslt)){
        resp = JSON.parse(rslt);
      }
    }   
    return resp;
  }
};


export function convertArray2Obj(arr1, key) {
  key = key ? key : "id";
  var obj = {};
  if (arr1.length > 0) {
    arr1.map((s) => {
      obj[s[key]] = s;
    });
  } else {
    obj = null;
  }
  return obj;
}


export function convertArray2ObjGroupby(arr1, key) {
  key = key ? key : "id";
  var obj = {};
  if (arr1.length > 0) {
    arr1.map((s) => {
      if (!obj[s[key]]) {
        obj[s[key]] = [];
      }
      obj[s[key]].push(s);
    });
  } else {
    obj = null;
  }
  return obj;
}

export function convertObj2Array(obj) {
  var arr = [];
  obj &&
    ObjectKeys(obj).map((o) => {
      arr.push(obj[o]);
    });
  return arr;
}


export const parseQuery = (url) => {
  var urlParams = new URLSearchParams(url);
  var obj = {};
  var entries = urlParams.entries();
  for (var pair of entries) {
    obj[pair[0]] = pair[1];
  }
  return obj;
};






let storeCallbackOnMemory = {}


export function compareDependencies(name, params ) {
  let sum = 0
  params.forEach((dpn,ind2)=>{
    let v1 = storeCallbackOnMemory[name]["params"][ind2];
    if(typeof dpn === "object"){
      sum += JSON.stringify(v1)===JSON.stringify(dpn)?1:0;
    }
    else{
      sum += v1===dpn?1:0;
    }
  })
  return params.length === sum;
}



export function callbackOnMemory(name, params , cb ) {
  if(!storeCallbackOnMemory[name]){
    storeCallbackOnMemory[name] = {}
  }
  if(!storeCallbackOnMemory[name]["params"]){
    storeCallbackOnMemory[name]["params"] = params;
  }
  if(!storeCallbackOnMemory[name]["result"]){
    storeCallbackOnMemory[name]["result"] = cb();
  }
  
  if(compareDependencies(name, params )){
    return  storeCallbackOnMemory[name]["result"];
  }
  else{
    storeCallbackOnMemory[name]["params"] = params;
    storeCallbackOnMemory[name]["result"] = cb();
    return  storeCallbackOnMemory[name]["result"];
  }
}




