export const isWeekend = day =>{
  return day % 7 === 6 || day % 7 === 0;
}


export const getDayName = (day, month,year) =>{
  const date = new Date(Date.UTC(year,month,day));
  const options = {weekday:"short"};
  return new Intl.DateTimeFormat("en-US",options).format(date);
}


export const renderCalendar = (month, year) =>{
  let week = [], days = [];
  const lastDay =  MaxDayperMonth(year,month+1);

  const firstDayIndex = new Date(
    year,
    month,
    1
  ).getDay();


  const lastDayIndex = new Date(
    year,
    month,
    lastDay
  ).getDay();

  const nextDays = 7 - lastDayIndex - 1;


  for (let x = firstDayIndex; x > 0; x--) {
    days.push({
      day:"",
      isActive:false,
      prevLastDay: true
    })
    // days = prevLastDay - x + 1
  }

  for (let i = 1; i <= lastDay; i++) {
    if (
      i === new Date().getDate() &&
      month === new Date().getMonth() &&
      year === new Date().getFullYear() 
    ) {
      days.push({
        day:i,
        isToday:true,
        isActive:false
      })
    } else {
      days.push({
        day:i,
        isActive:false
      })
    }
    const currentDayIndex = new Date(
      year,
      month,
      i
    ).getDay();
    if(currentDayIndex === 6){
      week.push(days);
      days = [];
    }
  }

  for (let j = 1; j <= nextDays; j++) {
    days.push({
      day:"",
      nextDay:true,
      isActive:false
    })
    if(j=== nextDays){
      week.push(days);
      days = [];
    }
  }

  
  return week;
}




export const parseDateString = (yyyy,mm,dd) =>{
  let m = mm*1>9?mm:"0"+mm;
  let d = dd*1>9?dd:"0"+dd;
  return m+"/"+d+"/"+yyyy;
}



export function febMaxDays(year) {
  if (year % 100 !== 0) {
    if (year % 4 === 0 || year % 400 === 0) {
      return 29;
    } else {
      return 28;
    }
  } else {
    return 28;
  }
}

export function MaxDayperMonth(yyyy, mm) {
  if (!isNotEmpty(yyyy) || !isNotEmpty(mm)) {
    return null;
  }
  var y = parseInt(yyyy.toString()),
    m = parseInt(mm.toString());
  let _dayPM = [0, 31, febMaxDays(y), 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
  return _dayPM[m];
}



